export default {
  data() {
    return {
      titulo1: false,
      titulo2: false,
      titulo3: false,
      texto1: false,
      texto2: false,
      texto3: false,
      footer: false,
    };
  },
  methods: {},
  created() {
    setTimeout(() => {
      this.titulo1 = true;
    }, 500);
    setTimeout(() => {
      this.texto1 = true;
    }, 1000);
    setTimeout(() => {
      this.titulo2 = true;
    }, 1500);
    setTimeout(() => {
      this.texto2 = true;
    }, 2000);
    setTimeout(() => {
      this.titulo3 = true;
    }, 2500);
    setTimeout(() => {
      this.texto3 = true;
    }, 3000);
    setTimeout(() => {
      this.footer = true;
    }, 3500);
  },
};
