export default {
  data() {
    return {
      showTitulo: false,
      showText: false,
      separator: false,
      divisor: false,
    };
  },
  created() {
    setTimeout(() => {
      this.showTitulo = true;
    }, 500);
    setTimeout(() => {
      this.showText = true;
    }, 1000);
    setTimeout(() => {
      this.separator = true;
    }, 1500);
    setTimeout(() => {
      this.divisor = true;
    }, 2000);
  },
};
