export default {
  data() {
    return {
      emailLabel: false,
      email: false,
      redesLabel: false,
      img: false,
    };
  },
  methods: {
    redirect(url) {
      window.open(url);
    },
  },
  created() {
    setTimeout(() => {
      this.emailLabel = true;
    }, 500);
    setTimeout(() => {
      this.email = true;
    }, 1000);
    setTimeout(() => {
      this.redesLabel = true;
    }, 1500);
    setTimeout(() => {
      this.img = true;
    }, 2000);
  },
};
